}

.galleryWidget .wrapper > .inner {
  padding: 0 20px;
  height: 100%;
}

.galleryWidget .wrapper img {
  -webkit-backface-visibility: hidden;
  width: 100%;
  height: 100% !important;
  object-fit: cover;
}

.galleryWidget .item.active .wrapper {
  top: 0px;
  position: relative;
}

.galleryWidget .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.galleryWidget .content a {
  display: block;
}

.galleryWidget .content.style_01 .wrap {
  display: table;
  width: 100%;
  table-layout: fixed;
  height: 100%;
}

.galleryWidget .content.style_01 .wrap > * {
  display: table-cell;
  vertical-align: middle;
  text-align: left;
}

.galleryWidget .content.style_01 .wrap > * {
  text-align: center;
}

.galleryWidget .content.style_01 h2 {
  font-family: ff-cocon-pro, sans-serif;
  font-weight: 300;
  font-style: normal;
  line-height: 1em;
  font-size: 2.4rem;
  color: #fff;
  letter-spacing: .01em;
  position: relative;
  opacity: 0;
  transition: .8s;
}

.galleryWidget .item.move .content.style_01 h2 {
  opacity: 1;
}

.galleryWidget .content.style_02 .wrap {
  display: table;
  width: 100%;
  table-layout: fixed;
  width: 50%;
  height: 100%;
  margin: 0 0 0 auto;
  transition: .8s;
  opacity: 0;
}

.galleryWidget .content.style_02 .wrap > * {
  display: table-cell;
  vertical-align: middle;
  text-align: left;
}

.galleryWidget .content.style_02 .wrap > * {
  text-align: center;
