#mainNav {
  position: fixed;
  left: 40px;
  bottom: 14px;
  width: 120px;
  height: 120px;
  z-index: 700;
  cursor: pointer;
  transform-origin: 0 100%;
  transition: 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
}

#mainNav::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 100%;
  background-color: #fff;
  width: 100%;
  height: 100%;
  transition: width 0.5s cubic-bezier(0.645, 0.045, 0.355, 1), height 0.5s cubic-bezier(0.645, 0.045, 0.355, 1), filter 0.5s cubic-bezier(0.645, 0.045, 0.355, 1), border-radius 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
}

#mainNav:hover::before {
  filter: blur(5px);
}

#mainNav > .inner {
  position: relative;
